
import { getList } from 'api/article'
import slider from 'vue-concise-slider'
import ContentHeader from '../components/content-header'
import { mapGetters } from 'vuex'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
name: 'index',
components: {
ContentHeader,
slider,
XButton
},
data () {
return {
vist_list: [
{logo:'./static/img/logo_1.png',name:'EOS'}
],
member_list: [
{logo:'./static/img/avatar_10.png',name:'RENDAN<br>BLUMER',job:'CEO'},
{logo:'./static/img/avatar_9.png',name:'DANEL<br>LARMER',job:'CTO'},
{logo:'./static/img/avatar_8.png',name:'RENDAN<br>BLUMER',job:'CFO'},
{logo:'./static/img/avatar_7.png',name:'XIAO LAI<br>LI',job:''}
],
coin_people_list: ['./static/img/avatar_1.png', './static/img/avatar_2.png', './static/img/avatar_3.png', './static/img/avatar_4.png', './static/img/avatar_5.png', './static/img/avatar_6.png'],
timeArr: ['201857', '2018615', '2018616', '2018621'],
time: '',
pages:[
{
html: `<div class="slider">
<p>EOS 2165eos1112EOS17...</p>
<p class="title"></p>
</div>`,
},
{
html: `<div class="slider">
<p>Block.oneDaniel LarimerDAppsEOSIOEOSIO Dawn 4.0...</p>
<p class="title"></p>
</div>`,
},
{
html: `<div class="slider">
<p>EOS486166EOS5EOSTho...</p>
<p class="title"></p>
</div>`,
},
{
html: `<div class="slider">
<p>ECAFEOSEOS...</p>
<p class="title"></p>
</div>`,
}
],
sliderinit: {
currentPage: 0,
thresholdDistance: 100,
thresholdTime: 300,
loop:true,
infinite:1,
slidesToScroll:1
},
join_status: false,
community: '',
haveMeanus: false,
count: '1024',
meanusTxt: '',
curent: 0,
naves :['','','',''],
listQuery: {
page: 1,
limit: 10,
type: 'EOS',
aspect: '',
id:''
},
list:  [],
total: null,
}
},
created() {
this.$store.dispatch('InitJoin', '')
this.getList();
},
computed: {
...mapGetters([
'join',
])
},
methods: {
getList() {
this.community = this.join ? '' : ''
this.join_status = this.join
getList(this.listQuery).then(response => {
this.list = response.data.items;
this.total = response.data.total;
})
},
onInit (data) {
this.time = this.timeArr[data.currentPage]
},
slide (data) {
let len = this.timeArr.length
let current
if( data.currentPage == len ) {
current = 0
} else if (data.currentPage == '-1') {
current = len - 1
} else {
current = data.currentPage
}
this.time = this.timeArr[current]
},
onItemClick (index) {
this.curent = index
let aspect = index == '0'? '': this.naves[index]
this.listQuery.aspect = aspect
getList(this.listQuery).then(response => {
this.list = response.data.items;
this.total = response.data.total;
})
},
onDevoteClick (){
this.$vux.toast.text('', 'middle');
},
onOpenMeanus (){
this.haveMeanus = !this.haveMeanus
this.meanusTxt = this.haveMeanus ? '' : ''
},
communityAdd () {
var that= this
if (!this.join_status) {
that.$store.dispatch('UpdateJoin', {join: true}).then(() => {
this.join_status = true
this.community = ''
this.$vux.toast.show({
type: 'success',
text: '',
})
})
}
}
}
}



